function App() {
  const [current, send] = useMachine(stateMachine)

  const handleNext = () => {
    send("NEXT")
  }

  return (
    <React.Fragment>
      <div className="main">
        <div class="card">
          <div class="tools">
            <div class="circle">
              <span class="red box"></span>
            </div>
            <div class="circle">
              <span class="yellow box"></span>
            </div>
            <div class="circle">
              <span class="green box"></span>
            </div>
          </div>
          <div class="card__content">
            <center>
              <h1>Current State: {current.context.status}</h1>
              <h3>Time : {current.context.time}</h3>
              {current.context.imgLink && (
                <img src={current.context.imgLink} alt={current.value}></img>
              )}
            </center>
          </div>
        </div>
        <center>
          <button onClick={handleNext}>NEXT STATE</button>
        </center>
      </div>
    </React.Fragment>
  )
}

export default App
